body {
	font-family: 'Balsamiq Sans', cursive;
	background-color: #191a1b;
	color: #ece8e2;
	font-size: 19px;
}
h2, p{ margin-top: 6px; margin-bottom: 6px; }
a {
    color: #3992f8;
	text-decoration: none;
}
a:hover {
  color: #63aafa;
}

.topnav {
	background-color: #333;
	overflow: hidden;
	font-size: 20px;
}

.topnav a {
	float: right;
	display: block;
	color: #dfdfdf;
	text-align: center;
	padding: 14px 16px;
}

.topnav a:hover {
	background-color: #111;
}

.topnav .logo {
  float:left;
  font-weight:bold;
}

.container {
	background-color: #222324;
	padding: 20px;
	margin: 20px 7px 20px 7px;
	overflow: auto;
	text-align: center;
}

.container a:hover{
	text-decoration-line: underline;
}

.line{
	border: 1px solid #ece8e2;
	margin: 10px 0 10px 0;
}

#box{
  display:grid;
  grid-template-columns: 3fr 1fr;
}

@media screen and (max-width: 600px) {
	.topnav a,.topnav .logo {
		float: none;
	}
	#box{
  grid-template-columns: 1fr;
  }
  #main{
  margin-bottom:7px;
  }
  #sidebar{
  margin-top:7px;
  }
}

